import React from "react";

const LoseScreen = ({ achieved, question }) => {
  return (
    <div className="lose-screen">
      <div className="announcement">
        {question[0].year === "1953" ? (
          <h2>,  !</h2>
        ) : (
          <h2> ,    </h2>
        )}
      </div>
      <div className="achievements">
             : ( )
      </div>
    </div>
  );
};

export default LoseScreen;
