import React, { useEffect, useState } from "react";
import "../../styles/main/rules.css";
import { CSSTransition } from "react-transition-group";

const Rules = ({ setIsShowRules }) => {
  const [show, setShow] = useState(false);

  useEffect(() => {
    setShow(true);
  }, []);
  return (
    <div className="rules">
      <div className="upper-part">
        <div className="years">
          <div>
            1900
            <br />
            &emsp;-1953
          </div>
          <p> </p>
        </div>
        <div className="title">     </div>
        <div className="start-button">
          <button onClick={() => setIsShowRules(false)}>
            <img
              src={process.env.PUBLIC_URL + `/images/red-arrow.png`}
              alt="arrow"
            />
          </button>
          <p> </p>
        </div>
      </div>
      <hr className="hr" />
      <div className="description">
        <div className="left">
          <CSSTransition in={show} timeout={300} classNames="alert">
            <h3> &laquo;,    1900 !&raquo;</h3>
          </CSSTransition>
          <p>
                ,     
              1900 .      ,
                     
                   
             XX .
          </p>
          <hr className="small-hr" />
        </div>
        <div className="right">
          <h4 className="red">
                     1953 !
          </h4>
          <p>
                  .  
                   !
            !
          </p>
        </div>
      </div>
    </div>
  );
};

export default Rules;
